import axios from "axios";

const API_URL = "https://backgammoncloud.com/api/auth/";
// const API_URL = "https://159.223.142.17/api/auth/";
// const API_URL = "http://localhost:3001/api/auth/";
// const API_URL = "http://localhost:8080/api/auth/";

class AuthService {
  login(username: string, password: string) {
    return axios
      .post(API_URL + "signin", {
        username,
        password
      })
      .then(response => {
        if (response.data.accessToken) {
          localStorage.setItem("user", JSON.stringify(response.data));
        }

        return response.data;
      });
  }

  logout() {
    localStorage.removeItem("user");
  }

  register(username: string, email: string, password: string, country: string, state: string, gender: string, birthday: string, role: string) {
    return axios.post(API_URL + "signup", {
      username,
      email,
      password,
      country,
      state,
      gender,
      birthday, 
      role,
    });
  }

  getCurrentUser() {
    const userStr = localStorage.getItem("user");
    if (userStr) return JSON.parse(userStr);

    return null;
  }
}

export default new AuthService();
